import bInput from "components/Common/bInput.vue";
import bList from "components/Common/bList.vue";
import bListItem from "components/Common/bListItem.vue";
import bListItemSection from "components/Common/bListItemSection.vue";
import { mapActions, mapGetters } from "vuex";

export default {
  name: "UsersRatingList",
  data() {
    return {
      sortRatingDesc: true,
      sortAgeDesc: true,
      searchField: ""
    };
  },
  components: {
    bInput,
    bList,
    bListItem,
    bListItemSection
  },
  methods: {
    ...mapActions({
      getUsers: "users/getUsersAPI"
    }),
    initials(name, surname) {
      return (name[0] + surname[0]).toUpperCase();
    },
    userAvatarStyle(user) {
      let background = "background:" + user.color + ";";
      let positionStyle = "";
      switch (user.position) {
        case 1:
          positionStyle = "width: 65px; height: 65px";
          break;
        case 2:
          positionStyle = "width: 60px; height: 60px";
          break;
        case 3:
          positionStyle = "width: 55px; height: 55px";
          break;
        default:
          positionStyle = "";
      }
      return background + positionStyle;
    },
    wreathImg(pos) {
      return pos === 1
        ? "users-rating-list__avatar--top-1"
        : pos === 2
        ? "users-rating-list__avatar--top-2"
        : pos === 3
        ? "users-rating-list__avatar--top-3"
        : "";
    },
    ratingAscSorting: (a, b) => a.rating - b.rating,
    ratingDescSorting: (a, b) => b.rating - a.rating,
    ageAscSorting: (a, b) => a.age - b.age,
    ageDescSorting: (a, b) => b.age - a.age,
    ratingTitle(rating) {
      let str = "" + rating;
      let label = "";
      switch (str[str.length - 1]) {
        case "0":
          label = "баллов";
          break;
        case "1":
          label = str[str.length - 2] === 1 ? "баллов" : "балл";
          break;
        case "2":
          label = str[str.length - 2] === 1 ? "баллов" : "балла";
          break;
        case "3":
          label = str[str.length - 2] === 1 ? "баллов" : "балла";
          break;
        case "4":
          label = str[str.length - 2] === 1 ? "баллов" : "балла";
          break;
        case "5":
          label = "баллов";
          break;
        case "6":
          label = "баллов";
          break;
        case "7":
          label = "баллов";
          break;
        case "8":
          label = "баллов";
          break;
        case "9":
          label = "баллов";
          break;
        default:
          label = "";
      }
      return label;
    }
  },
  computed: {
    ...mapGetters({
      usersList: "users/getUsers"
    }),
    ratingList() {
      let arr = this.usersList
        .filter(
          user =>
            user.name.indexOf(this.searchField) !== -1 ||
            user.secondName.indexOf(this.searchField) !== -1
        )
        .sort(this.sortAgeDesc ? this.ageDescSorting : this.ageAscSorting)
        .sort(
          this.sortRatingDesc ? this.ratingDescSorting : this.ratingAscSorting
        );
      return arr;
    }
  },
  mounted() {
    this.getUsers();
  }
};
