<template>
  <div class="b-list-item-section" :class="classes">
    <slot></slot>
  </div>
</template>

<script>
export default {
  name: "bListItemSection",
  data() {
    return {
      classes: ""
    };
  },
  props: {
    side: {
      type: Boolean,
      default: false
    },
    avatar: {
      type: Boolean,
      default: false
    },
    top: {
      type: Boolean,
      default: false
    }
  },
  mounted() {
    this.classes += this.side ? "b-list-item-section--side " : "";
    this.classes += this.avatar ? "b-list-item-section--avatar " : "";
    this.classes += this.top ? "b-list-item-section--top " : "";
    this.classes =
      !this.side && !this.avatar && !this.top
        ? "b-list-item-section--main"
        : this.classes;
  }
};
</script>

<style>
.b-list-item-section {
  display: flex;
  align-items: center;
}
.b-list-item-section--main {
  flex: 10000 1 0;
}
.b-list-item-section--side {
  display: flex;
  min-width: 50px;
  width: auto;
  padding-right: 16px;
  justify-content: center;
}
.b-list-item-section--avatar {
  min-width: 70px;
}
.b-list-item-section--top {
  align-items: flex-start;
}
</style>
