<template>
  <div class="b-input">
    <input
      class="b-input__field"
      type="text"
      :value="value"
      @input="__updateValue($event.target.value)"
    />
    <div class="b-input__icon">
      <slot name="icon"></slot>
    </div>
  </div>
</template>

<script>
export default {
  name: "bInput",
  data() {
    return {};
  },
  props: {
    value: [String, Number]
  },
  methods: {
    __updateValue: function(value) {
      this.$emit("input", value);
    }
  }
};
</script>

<style>
.b-input {
  display: flex;
  width: 100%;
}
.b-input__icon {
  display: flex;
  justify-content: center;
  align-items: center;
  min-width: 40px;
  width: auto;
  padding-left: 16px;
}
.b-input__field {
  padding: 8px;
  font-size: 20px;
  flex: 10000 1 0;
}
</style>
